.grid-container {
    margin: 10% auto;
}

.grid {
    white-space: pre;
}

button {
    margin-top: 0;
    padding-top: 0;
    margin: 2px;
}

ul {
    list-style-type: none;
    margin: 0;
    padding: 0;
}

li {
    float: left;
}

a {
    display: block;
    padding: 8px;
    background-color: #dddddd;
}